import { ReactNode } from 'react';
import NextLink from 'next/link';

const blankStyles = {
  color: 'white',
  textDecoration: 'none',
};

const Link = ({
  children,
  href,
  style,
}: {
  children: ReactNode;
  href: string;
  style?: StyleSheet;
}) => (
  <NextLink href={href}>
    {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
    <a style={style}>{children}</a>
  </NextLink>
);

export default Link;
